<template>
  <v-layout row wrap>
    <v-flex xs12>
      <v-card>
        <v-navigation-drawer class="mr-0 pr-0" :mini-variant.sync='mini' app stateless hide-overlay clipped v-model='drawer' width=400>
          <v-toolbar flat dark color='secondary'>
            <v-toolbar-side-icon @click.stop="mini=!mini,items.forEach((el,i)=>{if(mini==true){items[i].active=0}else{items[i].active=1}})" large/>
            <v-toolbar-title>Simulator</v-toolbar-title>
            <v-spacer/>
            <v-btn flat icon @click.stop="mini=!mini,items.forEach((el,i)=>items[i].active=0)">
              <v-icon>chevron_left</v-icon>
            </v-btn>
          </v-toolbar>
          <v-list expand>
            <v-list-group
              pt-0 mt-0
              v-for="item in items"
              :key="item.title"
              v-model="item.active"
              :prepend-icon="item.action"
              no-action
            >
              <template v-slot:activator>
                <v-list-tile>
                  <v-list-tile-content>
                    <v-list-tile-title class='title'>{{ item.title }}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </template>

              <v-list-tile
                v-for="subItem in item.items"
                :key="subItem.title"
              >
                <v-flex xs12>
                  <v-list-tile-content>
                    <v-text-field class='title' v-if="subItem.type=='input'" v-model=subItem.value type="number" :label=subItem.title></v-text-field>
                    <v-text-field class='title' v-else-if="subItem.type=='input-text'" v-model=subItem.value type="text" :label=subItem.title></v-text-field>
                    <v-layout justify-center row wrap v-else-if="subItem.type=='select'">
                      <v-flex xs9>
                        <v-tooltip bottom>
                          <template v-slot:activator="{on}">
                            <!-- :class=subItem.color -->
                            <v-combobox v-model=subItem.value v-on="on" :label=subItem.title :items=subItem.items @change="(a)=>{subItem.efficiency = a.value; inputChange(subItem)}"></v-combobox>
                          </template>
                          <span class='title'>{{subItem.value.text}}</span>
                        </v-tooltip>
                      </v-flex>
                      <v-spacer/>
                      <v-flex xs3 text-xs-center>
                        <!-- :class=subItem.color -->
                        <v-text-field type='number' min="0.01" v-model=subItem.efficiency :label=subItem.units  @input="inputChange(subItem)"></v-text-field>
                      </v-flex>
                    </v-layout>
                    <v-select class='title' v-on="on" v-model=subItem.value v-else-if="subItem.type=='select2'" :items=subItem.items :label=subItem.title></v-select>
                  </v-list-tile-content>
                </v-flex>
                <v-list-tile-action>
                  <v-icon>{{ subItem.action }}</v-icon>
                </v-list-tile-action>
              </v-list-tile>
            </v-list-group>
          </v-list>
        </v-navigation-drawer>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data: () => ({
    drawer: true,
    mini: false,
    items: [
          {
            active: 1,
            action: 'location_city',
            title: 'Building Design',
            items: [
              { title: 'Location',
                type: 'select2',
                items: ['Atlanta, GA','Boston, MA','Pheonix, AZ','Seatle, WA','Los Angeles, CA','Miami, FL'],
                value: 'Atlanta, GA'
              },
              { title: 'Weather',
                type: 'select2',
                items: ['no change','+1 degF','+2 degF','+3 degF','+4 degF','+5 degF'],
                value: 'no change'
              },
              { title: 'Size (gross sqft)',
                type: 'input',
                value: 37000
              },
              { title: 'Cistern Size (gallons)',
                type: 'input',
                value: 50000
              },
              { title: 'Solar Array Power (kW)',
                type: 'input',
                value: 330},
              { title: 'Operations (daily student hours)',
                type: 'input',
                value: 2000}
            ]
          },
          {
            active: 1,
            action: 'waves',
            title: 'Water Technology',
            items: [
              { title: 'Toilet Type',
                type: 'select',
                items: [{text: 'KBISD Foam Flush Composting', value: 0.04},{text: 'Niagara Ultra High Eff.',value: 0.8},{text: 'American Std Lexington',value: 1.28},{text: 'Glacier Bay High Eff. Dual Flush', value: 1.6}],
                value: {text: 'KBISD Foam Flush Composting', value: 0.04},
                efficiency: 0.04,
                units: 'gal',
                color: 'amber lighten-5'
              },
              { title: 'Faucet Type',
                type: 'select',
                items: [{text:'Sloan SF-2350',value: 0.09},{text: 'KBISD Sink',value: 0.13}],
                value: {text:'Sloan SF-2350',value: 0.09},
                efficiency: 0.09,
                units: 'gal',
                color: 'transparent'
              },
            ]
          },
          {
            active: 1,
            action: 'battery_charging_full',
            title: 'Energy Technology',
            items: [
              { title: 'Lightbulb Type',
                type: 'select',
                items: [{text: 'LED', value: 9.5},{text: 'CFL', value: 13},{text: 'Incandescent',value: 60}],
                value: {text: 'LED', value: 9.5},
                efficiency: 10,
                units: 'Watts',
                color: 'transparent'
              },
              { title: 'HVAC Type',
                type: 'select2',
                items: [{text: 'VAV + Radiant Floors', value: 0.04},{text: 'VAV only',value: 0.8}],
                value: {text: 'VAV + Radiant Floors', value: 0.04},
                efficiency: 1,
                units: 'Watts',
                color: 'transparent'
              },
              { title: 'Windows',
                type: 'select',
                items: [{text:'Single Pane',value: 100},{text: 'Double Pane',value: 80},{text: 'Triple Pane',value: 60}],
                value: {text:'Single Pane',value: 100},
                efficiency: 1,
                units: '% Energy Loss',
                color: 'transparent'
              },
            ]
          },
        ],
  }),
  methods: {
    display(msg){
      console.log(msg)
    },
    inputChange(subItem){
      if(subItem.title == 'Toilet Type'){
        console.log('Changing Toilet Type');
        this.$store.commit('changeToiletType',this.items[1].items[0]);
        // console.log(this.$store.state.water.toilet)
        // console.log(this.$store.state.water.kFactors.toilets)
      }
    },
  },
  // watch: {
  //   items: function(newItem,oldItem){
  //     this.inputChange(newItem)
  //   },
  // },
  computed: {
  },
  beforeMount() {
    // this.drawer = this.$vuetify.breakpoint.lgAndUp
  }
}
</script>
